              <span class="w_radio_label" style="width:1rem;">顺产</span>
            </label>
            <label class="w_radiolist_label"> 
              <span class="w_radio">
                <input name="parturition" v-model="parturition" type="radio" class="w_radio_input" id="1" value="剖腹产">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:1.5rem;">剖腹产</span>
            </label>
          </div>
        </div>
      </a>
      <a class="w_cell">
