          <div class="w_cell_title">
            <span class="w_cell_text">生产胎数</span>
          </div>
          <div class="w_cell_value" style="border-bottom:none;">
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="radio" v-model="infantCount" name="infantCount" class="w_radio_input" value="单胎">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:1rem;">单胎</span>
            </label>
            <label class="w_radiolist_label"> 
              <span class="w_radio">
                <input name="infantCount" v-model="infantCount" type="radio" class="w_radio_input" value="双胎">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:1.5rem;">双胎</span>
            </label>
            <label class="w_radiolist_label"> 
              <span class="w_radio">
                <input name="infantCount" v-model="infantCount" type="radio" class="w_radio_input" value="多胎">
                <span class="w_radio_core"></span>
              </span>
