            <span class="w_cell_text">体重</span>
          </div>
          <div class="w_cell_value" >
            <span style="width:100%;"><input class="w_core" type="text" placeholder="多胞胎用逗号隔开 单位为kg" v-model="infantWeight"></span>
          </div>
        </div>
      </a>
      <a class="w_cell">
