          </div>
          <div class="w_cell_value">
            <span style="width:100%;"><input class="w_core" type="text" placeholder="多胞胎用逗号隔开 单位为CM" v-model="infantHeight"></span>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title" style="flex:3;">
            <span class="w_cell_text">产妇有无以下情况</span>
          </div>
          <div class="w_cell_value" style="border-bottom:none;">
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="maternalCases" name="maternalCases" class="w_radio_input" value="无">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:1rem;">无</span>
            </label>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_value" style="border-bottom:none;">
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="maternalCases"  class="w_radio_input" value="妊娠性糖尿病">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:3rem;">妊娠性糖尿病</span>
            </label>
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="maternalCases" class="w_radio_input" value="妊娠性高血压">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:3rem;">妊娠性高血压</span>
            </label>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_value" style="border-bottom:none;">
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="maternalCases"  class="w_radio_input" value="心脏病">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:3rem;">心脏病</span>
            </label>
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="maternalCases" class="w_radio_input" value="精神类疾病">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:3rem;">精神类疾病</span>
            </label>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_value" style="border-bottom:none;">
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="maternalCases"  class="w_radio_input" value="传染类疾病">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:3rem;">传染类疾病</span>
            </label>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_value" style="border-bottom:none;">
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="maternalCases"  class="w_radio_input" value="其他情况">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:2rem;">其他情况</span>
              <span style="border-bottom:2px solid #d06091"><input class="w_core" type="text" placeholder="" v-model="othermaternalCases"></span>
            </label>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title" style="flex:3;">
            <span class="w_cell_text">新生儿有无以下情况</span>
          </div>
          <div class="w_cell_value" style="border-bottom:none;">
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="babyCases" name="maternalCases" class="w_radio_input" value="无">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:1rem;">无</span>
            </label>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_value" style="border-bottom:none;">
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="babyCases"  class="w_radio_input" value="产钳夹出">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:3rem;">产钳夹出</span>
            </label>
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="babyCases" class="w_radio_input" value="呛羊水">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:3rem;">呛羊水</span>
            </label>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_value" style="border-bottom:none;">
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="babyCases"  class="w_radio_input" value="出生时不足37周">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:3rem;">出生时不足37周</span>
            </label>
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="babyCases" class="w_radio_input" value="先心病">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:3rem;">先心病</span>
            </label>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_value" style="border-bottom:none;">
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="babyCases"  class="w_radio_input" value="低体重儿">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:3rem;">低体重儿</span>
            </label>
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="babyCases" class="w_radio_input" value="巨大儿">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:3rem;">巨大儿</span>
            </label>
          </div>
        </div>
      </a>
      <a class="w_cell" style="margin-bottom:1rem;">
        <div class="w_cell_wraper">
          <div class="w_cell_value" style="border-bottom:none;">
            <label class="w_radiolist_label" style="margin-right:1.5rem;">
              <span class="w_radio" >
                <input type="checkbox" v-model="babyCases"  class="w_radio_input" value="其他情况">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label" style="width:2rem;">其他情况</span>
              <span style="border-bottom:2px solid #d06091"><input class="w_core" type="text" placeholder="" v-model="otherbabyCases"></span>
            </label>
          </div>
        </div>
      </a>
    </div>
    <div class="footer">
      <mt-button size="large" @click.native="handleNextClick">下一步</mt-button>
    </div>
 </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
import { Toast } from 'mint-ui';
export default {
  name: 'muYinEvaluate',
  data() {
    return {
      age: null,
      parturition: null,
      infantCount: null,
      maternalCases: [],
      othermaternalCases: '',
      otherbabyCases: '',
      babyCases: [],
      type: null,
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.age = to.query.age;
      vm.type = to.query.type;
    })
  },
  methods: {
    ...mapActions({
      changeInfantMom: 'changeInfantMom'
    }),
    handleNextClick: function() {
      if(!this.parturition) {
        Toast({
          message: '请选择生产方式',
          iconClass: 'icon icon-error',
          position: 'bottom',
          duration: 1000
        });
        return;
      }
      if(!this.infantCount) {
        Toast({
          message: '请选择生产胎数',
          iconClass: 'icon icon-error',
          position: 'bottom',
          duration: 1000
        });
        return;
      }
      if(!this.infantWeight) {
        Toast({
          message: '请填写新生儿体重',
          iconClass: 'icon icon-error',
          position: 'bottom',
          duration: 1000
        });
        return;
      }
      if(!this.infantHeight) {
        Toast({
          message: '请选择新生儿体长',
          iconClass: 'icon icon-error',
          position: 'bottom',
          duration: 1000
        });
        return;
      }
      let infantMom = {};

      if(this.parturition == '顺产') {
        infantMom.parturition = 1;
      } else if(this.parturition == '剖腹产') {
        infantMom.parturition = 2;
      }
      if(this.infantCount == '单胎') {
        infantMom.infantCount = 1;
      } else if(this.infantCount == '双胎') {
        infantMom.infantCount = 2;
      } else if(this.infantCount == '多胎') {
        infantMom.infantCount = -1;
      }

      infantMom.infantWeight = this.infantWeight.replace(/,/g,' ');
      infantMom.infantHeight = this.infantHeight.replace(/,/g,' ');
      infantMom.understanding = this.understanding;
      infantMom.maternalCases = this.maternalCases;
      infantMom.babyCases = this.babyCases;
      infantMom.maternalAge = this.age;
      if(this.othermaternalCases) {
        infantMom.maternalCases.forEach((item, index) => {
          if(item == '其他情况') {
            infantMom.maternalCases[index] = this.othermaternalCases
          }
        })
      } 
      if(this.otherbabyCases) {
        infantMom.babyCases.forEach((item, index) => {
          if(item == '其他情况') {
            infantMom.babyCases[index] = this.otherbabyCases
          }
        })
      }
      infantMom.infantSex = '';
      this.changeInfantMom(infantMom);
      if(this.type) {
        this.$router.push({ path: '/ysServicesList' })
      } else {
        this.$router.push({ path: '/myServicesList' })
      }
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.footer {
    height: 5%;
    z-index: 1;
}
</style>



